@import-normalize;
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500&display=block");


body {
  margin: 0;
  font-family: "Outfit", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: "Outfit", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  background: transparent;
  appearance: none;
  border: none;
  outline: none !important;
}

html,
body,
#root,
#rec-main-container {
  --headerHeight: 60px;
  position: relative;
  width: 100%;
  height: 100%;
  /* overscroll-behavior-block: none; */
  user-select: none;
  overflow: hidden;
  background: var(--secondaryColor);
  overflow: hidden;
}

#rec-main-container {
  overflow-y: auto;
  height: calc(100% - var(--headerHeight));
}

body {
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

a,
a:hover,
a:visited {
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
  cursor: pointer;
}

a:not(:has(.rec-header-button)):hover {
  border-bottom: 0.2px solid var(--primaryColor);
  border-top: 0.2px solid transparent;
}

.rec-main-scroll-container {}

.rec-page-section {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.rec-img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
  pointer-events: none
}

.rec-test-text {
  font-size: 110px;
  position: absolute;
  top: calc(50vh - 72px);
  left: 10vw;
  font-weight: 600;
  color: white;
}

.rec-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}