:root {
  --lightColor: white;
  --darkColor: black;

  --headerLogoWidth: 130px;
  --headerLogoHeight: 56px;
  --logoSize: 50px;
  --headerTop: 0;
  --headerRight: 14px;

  --logoWidth: var(--logoSize);
  --logoHeight: var(--logoSize);
  --logoScale: 1;
  --logoTransition: all 0.11s ease-in-out;
  --logoTransitionLong: 0.32s cubic-bezier(0.26, 0, 0.33, 1);

  --slideTransition: all 0.5s cubic-bezier(0.19, 0, 0.28, 0.99);
  --blowOutTransition: all 0.432s cubic-bezier(0.25, 0, 0, 0.99);
  --blowInTransition: all 0.432s cubic-bezier(0.685, 0.005, 0.53, 1);
  --menuTransitionIn: transform 0.4s cubic-bezier(0.25, 0, 0, 0.99);
  --menuTransitionOut: transform 0.4s cubic-bezier(0.685, 0.005, 0.53, 1);
  --menuLineTransitionPart: 0.38s cubic-bezier(0.25, 0, 0, 0.99);
  --menuLineTransitionOpenPart: 0.5s cubic-bezier(0.26, 0, 0.33, 1);


  --primaryColor: #0D0D0D;
  --secondaryColor: #FFFFFF;

  --iconFillColor: var(--secondaryColor);
  --iconStrokeColor: var(--primaryColor);
  --menuActiveColor: var(--secondaryColor);
  --menuGap: 1em;
  --menuItemTop: 51px;
  --menuLineGap: 24px;

  --uiGap: 6.2%;

  --slidePanelMinSize: 222px;
  --slidePanelBackground: #fafafa;
  --slidePanelTransition: 0.34s cubic-bezier(0.1, 0, 0.2, 1);
  --slidePanelZIndex: 100;
  --panelBoxShadowOut: 0px 0px 56px -56px rgba(59, 35, 6, 1);
  --panelBoxShadowIn: 0px 0px 138px -56px rgba(59, 35, 6, 1);
  --panelBoxShadowSmallIn: 0px 0px 138px -37px rgba(59, 35, 6, 1);
  --panelBoxShadowSmallOut: 0px 0px 37px -37px rgba(59, 35, 6, 1);
  --panelBoxShadowTinyIn: 0px 0px 138px -20px rgba(59, 35, 6, 1);
  --panelBoxShadowTinyOut: 0px 0px 20px -20px rgba(59, 35, 6, 1);
  --panelMargin: 0 var(--menuGap) 0 var(--menuGap);

  --panelInTransition: transform 0.432s cubic-bezier(.29, .03, .4, .99);
  --headerFilterTransition: filter 0.432s cubic-bezier(.29, .03, .4, .99);
  --panelOpacityTransition: opacity 0.2s cubic-bezier(.29, .03, .4, .99);
}

.rec-side-switch a {
  text-decoration: none;
  color: var(--iconStrokeColor);
}


.rec-side-switch {
  position: fixed;
  bottom: var(--headerTop);
  left: var(--headerRight);
  z-index: 2;
  width: var(--headerLogoWidth);
  height: var(--headerLogoHeight);
  color: var(--iconStrokeColor);
  transform: scale(0.625);

}

.rec-menu {
  position: absolute;
  right: 0;
  top: 41%;
  padding: 0;
  margin: 0;
  list-style: none;
  transform: translateY(-50%);
  width: 100%;
}

.rec-menu-item {
  position: relative;
  cursor: pointer;
  padding: 0 var(--menuGap);
  margin-bottom: 0.52em;
  text-decoration: none;
  width: 100%;
}

.rec-menu-item a {
  text-decoration: none;
  color: black;
  transition: color 0.3s ease-in-out;
}

.rec-menu-item:hover a,
.rec-menu-item.rec-selected a {
  color: var(--menuActiveColor);
}

.rec-menu-line {
  position: absolute;
  height: 1px;
  width: 0px;
  border-radius: 0.5px;
  top: calc(var(--menuItemTop) + var(--menuLineGap));
  background: var(--iconStrokeColor);
  transition: transform var(--menuLineTransitionPart),
    width var(--menuLineTransitionPart);
}

.rec-menu-line.rec-first-open {
  transition: transform var(--menuLineTransitionOpenPart),
    width var(--menuLineTransitionOpenPart);
}


/*
*
*   FOOTER
*


footer {
  position: fixed;
  left: var(--uiGap);
  bottom: 0;
  padding: 0.62rem 1rem;
  background: var(--iconFillColor);
  color: var(--iconStrokeColor);
}
*/

/*
*
*   SLIDE PANEL
*
*/

.rec-slide-panel {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  min-width: var(--slidePanelMinSize);
  min-height: var(--slidePanelMinSize);
  overflow: hidden;
  transition: var(--panelInTransition);
}

.rec-slide-panel-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: var(--panelInTransition);
}


.rec-slide-panel.rec-right {
  transform: translateX(100%);
}

.rec-slide-panel.rec-right .rec-slide-panel-container {
  transform: translateX(-100%);
}

.rec-slide-panel.rec-bottom {
  transform: translateY(100%);
}

.rec-slide-panel.rec-bottom .rec-slide-panel-container {
  transform: translateY(-100%);
}

/* open any panel */

.rec-slide-panel.rec-open {
  transform: translateX(0);
}

.rec-slide-panel.rec-open .rec-slide-panel-container {
  transform: translateX(0);
}

.rec-menu-item-text {
  text-align: right;
  display: flex;
  align-items: center;
}

.rec-menu-item-text div {
  margin-left: auto;
}

.rec-panel-bottom-menu {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

/*
*
*   BUTTONS
*
*/

button {
  cursor: pointer;
}

button:disabled {
  opacity: 0.4;
}

.rec-button-container-secondary {
  --color: black;
}

.rec-button-container-secondary button {
  color: var(--color);
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.6em 0.7em;
  flex: 1;
}

.rec-button-container-secondary button:hover {
  --color: var(--menuActiveColor);
}

.rec-button-container-secondary button svg {
  min-height: 22px;
}

.secondary-button {
  background-color: #fafafa;
  border-radius: 6px;
  padding: 0.34em 2em;
}

.rec-button-title {
  height: 100%;
  margin-top: 0.64em;
}

.rec-button {
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 36px;
  color: var(--primaryColor);
  background: var(--secondaryColor);
}

.rec-header-button {
  padding: 6px 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.rec-header-button-side-switch {
  margin-left: 36px;
  margin-right: 0;
  width: 150px;
}

.rec-header-button svg {
  margin-left: 7px;
}

/*
*
*   PAGE EDITOR
*
*/

.rec-editor-container {
  width: 100%;
  display: flex;
  justify-content: center;
  box-shadow: var(--panelBoxShadowSmallIn);
  background: var(--slidePanelBackground);
  padding: 0.8em 3em;
}

.rec-page-editor-container {
  position: fixed;
  left: 0;
  top: 0;
  padding: 0.3em 3em;
  z-index: 1;
  box-shadow: var(--panelBoxShadowTinyIn);
}

.rec-page-editor-container .rec-editor-buttons {
  max-width: 350px;
}

.rec-page-section-editor-container {
  position: absolute;
  left: 0;
  top: 0;
}

.rec-editor-buttons {
  width: 100%;
  max-width: 750px;
  display: flex;
  justify-content: center;
}

/*
*
*   IFRAME
*
*/


.iframe-container {
  width: 100%;
  height: 100%;
}

*:focus {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

.iframe-container iframe {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  background: transparent;
  margin: 0;
  padding: 0;
}

/*
*
*   HEADER
*
*/

.rec-header {
  position: absolute;
  background: var(--secondaryColor);
  color: var(--primaryColor);
  display: flex;
  align-items: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: var(--headerHeight);
  z-index: 1;
  transition: var(--headerFilterTransition);
  font-size: 17.5px;
}

.rec-header-container {
  display: flex;
  transition: var(--panelInTransition);
  width: 100%;
}

.rec-header-fade-container {
  flex: 1;
  display: flex;
  align-items: center;
}

.rec-header-breadcrumb {
  flex: 1;
  margin-left: 178px;
  font-weight: 200;
  letter-spacing: 0.2px;
  display: flex;
  align-items: center;
}

.rec-header-breadcrumb-details {
  flex: 1;
  display: flex;
  align-items: center;
  transition: var(--panelOpacityTransition);
}

.rec-header.rec-closed {}

.rec-header.rec-closed .rec-header-breadcrumb-details {
  opacity: 0;
}

.rec-header-breadcrumb a {
  font-weight: 400;
  border-color: var(--primaryColor);
}

.rec-menu-button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  margin-left: 10px;

}

.rec-menu-button.rec-disabled {
  opacity: 0.2;
  pointer-events: none;
}

.rec-menu-button-margin {
  margin-right: 18px;
}

.rec-spacer {
  --margin: 38px;
  margin-left: var(--margin);
  margin-right: var(--margin);
}

.rec-spacer-alt,
.rec-spacer-short {
  --margin: 8px;
}

.rec-icon-cross {
  position: absolute;
  width: 90%;
  height: 1.5px;
  background-color: var(--primaryColor);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

/*
*
*   INFO SECTION
*
*/

.rec-info-section {
  width: 100%;
  height: 100%;
  background-color: white;
}

.rec-info-section.rec-invert {
  color: white;
  background-color: rgba(0, 0, 0, 0.85);
}


.rec-info-container {
  --leftGap: 140px;
  --rightGap: 140px;
  --topGap: 140px;
  --bottomGap: 140px;
  position: relative;
  top: var(--topGap);
  left: var(--leftGap);
  width: calc(100% - var(--leftGap) - var(--rightGap));
  height: calc(100% - var(--topGap) - var(--bottomGap));
}

.rec-info-temp-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.rec-instruction-line {
  width: 100%;
  display: flex;
  font-size: 18px;
  align-items: center;
  margin-bottom: 6px;
}

.rec-instruction-icon {
  margin-right: 14px;
  display: flex;
  align-items: center;
}

/*
*
*   LOGO
*
*/


.rec-side-switch-logo {
  --logoFill: var(--iconFillColor);
  --logoStroke: var(--iconStrokeColor);
  position: relative;
  z-index: 1;
  pointer-events: none;
  width: 100%;
}

.rec-side-switch-logo svg {
  width: 40px;
  height: 53px;
  overflow: visible;
}

.rec-side-switch-logo-click-area {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  pointer-events: auto;
}

.rec-logo-graphics {
  overflow: visible;
}

.rec-logo-svg-fill {
  fill: var(--logoFill);
  transition: var(--logoTransition);
}

.rec-logo-svg-outline {
  fill: transparent;
  stroke: var(--logoStroke);
  stroke-width: 1.62;
}

.rec-logo-svg-element {
  position: absolute;
  left: 50%;
  transform-origin: 50% 50%;
  transition: var(--blowInTransition);
}

/*
*
*   MENU
*
*/

.rec-menu-panel {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rec-menu-panel a {
  pointer-events: all;
}

.rec-main-menu {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 110px;
}

.rec-main-menu li {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

}

.rec-main-menu-item {
  font-size: 52px;
  font-weight: 200;
  text-align: right;
}

.rec-sub-menu {
  margin-top: 60px;
  font-weight: 300;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.rec-sub-menu-item {
  font-size: 18px;
  margin-bottom: 25px;
  margin-right: 4px;
}

/*
*
*   POPUP
*
*/

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


.rec-open-panel {
  opacity: 1;
  pointer-events: auto;
}

.rec-closed-panel {
  opacity: 0;
  pointer-events: none;
}

.rec-popup-backdrop {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* background: radial-gradient(rgba(0, 0, 0, 0.4), transparent); */
  background: rgba(0, 0, 0, 0.4);
  transition: all 0.28s ease-out;
}

.rec-popup-backdrop.rec-popup-portal {
  position: fixed;
  z-index: 1000;
}

.rec-popup-container {
  display: flex;
  flex-direction: column;
  background: var(--secondaryColor);
  max-width: 90%;
  max-height: 90%;
  width: 700px;
  height: 900px;
  padding: 72px 99px;
  overflow: auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: transform 0.45s ease-out;
}

.rec-closed-panel .rec-popup-container {
  transform: translate(-50%, calc(-50% + 20px));
}

.rec-popup-title {
  font-size: 46px;
  margin-bottom: 14px;
}

.rec-popup-authors {
  font-weight: 200;
  margin-bottom: 86px;
}

.rec-popup-authors a {
  font-weight: 400;
}

.rec-popup-content {
  flex: 1;
  overflow-y: auto;
}

.rec-popup-content-full {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.rec-popup-details {
  font-weight: 200;
  padding-bottom: 1px;
  line-height: 1.2;
  letter-spacing: .1px;
  overflow-y: auto;
  white-space: pre-wrap;
  user-select: text;
}

.rec-popup-instructions {
  display: flex;
  flex-direction: column;
  margin-bottom: 0px;
  flex: 1;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.rec-popup-separator {
  margin-top: auto;
}

.rec-popup-button {
  width: 100%;
  padding: 14px;
  display: flex;
  justify-content: center;
  background: var(--primaryColor);
  color: var(--secondaryColor);
  font-size: 22px;
}

.rec-popup-tab-bar {
  margin-bottom: 36px;
}


/*
*
*   TAB BAR
*
*/

.rec-tab-bar {
  width: 100%;
  display: flex;
}

.rec-tab-button {
  --defaultBorder: 1px solid transparent;
  --activeBorder: 1px solid var(--primaryColor);
  --transitionIn: all .37s ease-out;
  --transitionOut: all .3s ease-out;
  --transition: var(--transitionOut);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  padding: 0 6px;
  font-size: 15px;
  color: var(--secondaryColor);
  background-color: var(--primaryColor);
  user-select: none;
  flex: 1;
  transition: var(--transition);
  border-top: var(--defaultBorder);
  border-bottom: var(--defaultBorder);
}

.rec-tab-button:hover,
.rec-tab-button.rec-active {
  --transition: var(--transitionIn);
  color: var(--primaryColor);
  background-color: var(--secondaryColor);
  border-top: var(--activeBorder);
  border-bottom: var(--activeBorder);
}

.rec-tab-button:first-child {
  border-left: var(--defaultBorder);
  padding-right: 7px;
}

.rec-tab-button:first-child:hover,
.rec-tab-button:first-child.rec-active {
  border-left: var(--activeBorder);
}

.rec-tab-button:last-child {
  border-right: var(--defaultBorder);
  padding-left: 7px;
}

.rec-tab-button:last-child:hover,
.rec-tab-button:last-child.rec-active {
  border-right: var(--activeBorder);
}

.rec-tab-button svg {
  transition: var(--transition);
}

.rec-tab-button:hover svg,
.rec-tab-button.rec-active svg {
  fill: var(--primaryColor);
}

.rec-tab-button-icon {
  display: flex;
  align-items: center;
}

.rec-tab-button-text {
  margin-left: 8px;
}